import React, { useEffect, useState } from 'react'
import ClsSQLite from './ClsSQLite';
import { Box, TextField } from '@mui/material';
import { DataGrid, GridColDef, GridValueGetterParams } from '@mui/x-data-grid';

const columns: GridColDef[] = [
    { field: 'id', headerName: 'Codigo', width: 90 },
    {
      field: 'descricao',
      headerName: 'Descrição',
      width: 150,
      editable: true,
    },
    {
      field: 'vlunit',
      headerName: 'Vl.unit',
      width: 150,
      editable: true,
    },
    {
      field: 'quantidade',
      headerName: 'Quantidade',
      type: 'number',
      width: 110,
      editable: true,
    },
   
  ];
  
  const rows = [
    { id: 1, descricao: 'Snow', vlunit: 'Jon', quantidade: 35 },
    { id: 2, descricao: 'Lannister', vlunit: 'Cersei', quantidade: 42 },
    { id: 3, descricao: 'Lannister', vlunit: 'Jaime', quantidade: 45 },
    { id: 4, descricao: 'Stark', vlunit: 'Arya', quantidade: 16 },
    { id: 5, descricao: 'Targaryen', vlunit: 'Daenerys', quantidade: null },
    { id: 6, descricao: 'Melisandre', vlunit: null, quantidade: 150 },
    { id: 7, descricao: 'Clifford', vlunit: 'Ferrara', quantidade: 44 },
    { id: 8, descricao: 'Frances', vlunit: 'Rossini', quantidade: 36 },
    { id: 9, descricao: 'Roxie', vlunit: 'Harvey', quantidade: 65 },
  ];
  



export default function Cadastro({ clsDB }: { clsDB: ClsSQLite }) {

    return(
     <>
         <Box sx={{mt:'1%'}}>
                <TextField
                    id="outlined-textarea"
                    label="Codigo"
                    placeholder="Codigo" 
                    variant="outlined" 
                />
          </Box>
           <Box sx={{mt:'1%'}}>
                <TextField
                    id="outlined-textarea"
                    label="Nome"
                    placeholder="Nome"  
                    variant="outlined"
                />
            </Box>
            <Box sx={{mt:'1%'}}>
                <TextField
                    id="outlined-textarea"
                    label="Forma de Pagamento"
                    placeholder="Forma Pagamento"  
                    variant="outlined"
                />
            </Box>
              
            <Box sx={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}        
       
        checkboxSelection
        
      />
    </Box>
     </>
    )
}
